const hotspotData = () => {
  return axios.get('https://data.cityofnewyork.us/api/views/varh-9tsp/rows.json?accessType=DOWNLOAD')
    .then((res) => res.data.data)
    .catch(err => console.error(err))
}

export default hotspotData



